<template>
	<div id="comboMg">
		<el-card>
			<el-tabs v-model="activeName" @tab-click="handleClick">
				<el-tab-pane label="" name="1"></el-tab-pane>
				<el-tab-pane label="" name="2"></el-tab-pane>
			</el-tabs>

			<el-row v-show="activeName == '1'">
				<el-table
					:data="items">
					<el-table-column prop="name" label=""></el-table-column>
					<el-table-column label="">
						<template slot-scope="scope">
							<div>: {{scope.row.content.spu}}SPU</div>
							<div>: {{scope.row.content.cAccount}}</div>
							<div>: {{scope.row.content.time}}</div>
						</template>
					</el-table-column>
					<el-table-column prop="price" label="">
						<template slot-scope="scope">
							{{ scope.row.price }}/
						</template>
					</el-table-column>
					<el-table-column prop="count" label=""></el-table-column>
				</el-table>
			</el-row>
			
			<el-row v-show="activeName == '2'">
				<el-table
					:data="items2">
					<el-table-column prop="name" label=""></el-table-column>
					<el-table-column prop="desc" label=""></el-table-column>
					<el-table-column prop="price" label=""></el-table-column>
					<el-table-column prop="count" label=""></el-table-column>
				</el-table>
			</el-row>
		</el-card>
	</div>
</template>

<script>
export default {
	data () {
		return {
			activeName: '1',
			items: [
				{ name: '', content: { spu: 0, order: 0, cAccount: 0, time: 0 }, price: '', count: 0 },
				{ name: '', content: { spu: 0, order: 0, cAccount: 0, time: 0 }, price: '', count: 0 },
				{ name: '', content: { spu: 0, order: 0, cAccount: 0, time: 0 }, price: '', count: 0 },
				{ name: '', content: { spu: 0, order: 0, cAccount: 0, time: 0 }, price: '', count: 0 },
			],
			items2: [
				{ name: '', desc: 'Shopify', price: '150000', count: 0 },
				{ name: '', desc: '', price: '10/1000', count: 0},
				{ name: '', desc: '', price: '100/1/10000', count: 0},
				{ name: '', desc: '', price: '5/1/10000', count: 0},
			],
			serviceCount: 0,
			mailCount: 0
		}
	},
	mounted() {
		this.getItems()
	},
	methods: {
		handleClick (val) {
			if (this.activeName == 1) {
			}
		},
		getItems () {
			this.$apiCall('api.Admin.vipSummary', {}, (r) => {
				if (r.ErrorCode == 9999) {
					this.items2[0].count = r.Data.Results.serviceCnt
					this.items2[1].count = r.Data.Results.mailCnt
					this.items2[2].count = r.Data.Results.productCnt
					this.items2[3].count = r.Data.Results.subUserCnt
					for(let key in r.Data.Results.vipInfo) {
						this.items[key-1].name = r.Data.Results.vipInfo[key].name
						this.items[key-1].price = r.Data.Results.vipInfo[key].amount
						this.items[key-1].count = r.Data.Results.vipInfo[key].count
						this.items[key - 1].content = {
							spu: r.Data.Results.vipInfo[key].productCnt,
							order: r.Data.Results.vipInfo[key].dailyOrderCnt,
							cAccount: r.Data.Results.vipInfo[key].subUserCnt,
							time: r.Data.Results.vipInfo[key].orderReserveDay
						}
					}
				}
			})
		}
	}
}
</script>

